// frontend/src/app/admin/page.tsx
"use client";

import React, { useEffect, useState } from "react";
import axios from "axios";
import AdminLayoutClient from "./AdminLayoutClient";

interface DashboardData {
  totalJobs: number;
  totalApplications: number;
}

const BACKEND_URL = process.env.NEXT_PUBLIC_API_BASE || "http://localhost:5000";

export default function AdminPage() {
  const [loading, setLoading] = useState(true);
  const [adminVerified, setAdminVerified] = useState(false);
  const [dashboardData, setDashboardData] = useState<DashboardData | null>(null);
  const [fetchError, setFetchError] = useState<string | null>(null);

  const [loginEmail, setLoginEmail] = useState("");
  const [loginPassword, setLoginPassword] = useState("");
  const [loginError, setLoginError] = useState("");

  const [accessToken, setAccessToken] = useState<string | null>(null);

  /** 1️⃣ On mount, try to refresh token */
  useEffect(() => {
    const initAuth = async () => {
      try {
        setLoading(true);
        const res = await axios.post(
          `${BACKEND_URL}/api/admin/refresh`,
          {},
          { withCredentials: true }
        );

        if (res.data?.token) {
          setAccessToken(res.data.token);

          // fetch /me using access token
          const meRes = await axios.get(`${BACKEND_URL}/api/admin/me`, {
            headers: { Authorization: `Bearer ${res.data.token}` },
          });

          if (meRes.data?.success) setAdminVerified(true);
          else setAdminVerified(false);
        } else {
          setAdminVerified(false);
        }
      } catch {
        setAdminVerified(false);
      } finally {
        setLoading(false);
      }
    };

    initAuth();
  }, []);

  /** 2️⃣ Fetch dashboard data once verified */
  useEffect(() => {
    if (!adminVerified || !accessToken) return;

    const fetchDashboard = async () => {
      try {
        setLoading(true);
        const [jobsRes, appsRes] = await Promise.all([
          axios.get(`${BACKEND_URL}/api/admin/jobs/count`, {
            headers: { Authorization: `Bearer ${accessToken}` },
          }),
          axios.get(`${BACKEND_URL}/api/admin/applications/count`, {
            headers: { Authorization: `Bearer ${accessToken}` },
          }),
        ]);

        setDashboardData({
          totalJobs: jobsRes.data?.count || 0,
          totalApplications: appsRes.data?.count || 0,
        });
      } catch {
        setFetchError("Failed to load dashboard data.");
      } finally {
        setLoading(false);
      }
    };

    fetchDashboard();
  }, [adminVerified, accessToken]);

  /** 3️⃣ Handle login submit */
  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoginError("");

    if (!loginEmail || !loginPassword) {
      setLoginError("Email and password are required");
      return;
    }

    try {
      setLoading(true);
      const res = await axios.post(
        `${BACKEND_URL}/api/admin/login`,
        { email: loginEmail, password: loginPassword },
        { withCredentials: true }
      );

      if (res.data?.success && res.data?.token) {
        setAccessToken(res.data.token);

        // fetch /me
        const meRes = await axios.get(`${BACKEND_URL}/api/admin/me`, {
          headers: { Authorization: `Bearer res.data.token` },
        });

        if (meRes.data?.success) setAdminVerified(true);
        else setAdminVerified(false);

        setLoginEmail("");
        setLoginPassword("");
      } else {
        setLoginError(res.data?.message || "Login failed");
      }
    } catch (err: unknown) {
      if (axios.isAxiosError(err)) {
        setLoginError(err.response?.data?.message || "Server error");
      } else if (err instanceof Error) {
        setLoginError(err.message);
      } else {
        setLoginError("Unexpected error");
      }
    } finally {
      setLoading(false);
    }
  };

  /** 4️⃣ Loading screen */
  if (loading) {
    return (
      <div className="flex items-center justify-center h-screen text-gray-500">
        Loading...
      </div>
    );
  }

  /** 5️⃣ Login form if not verified */
  if (!adminVerified) {
    return (
      <div className="flex items-center justify-center h-screen bg-gray-100 p-4">
        <form
          onSubmit={handleLogin}
          className="bg-white shadow-lg rounded-xl p-8 w-full max-w-md"
        >
          <h1 className="text-2xl font-bold mb-6 text-center">Admin Login</h1>
          {loginError && <p className="text-red-600 mb-4">{loginError}</p>}

          <label className="block mb-2 font-medium">Email</label>
          <input
            type="email"
            value={loginEmail}
            onChange={(e) => setLoginEmail(e.target.value)}
            className="w-full p-2 border rounded mb-4"
            required
          />

          <label className="block mb-2 font-medium">Password</label>
          <input
            type="password"
            value={loginPassword}
            onChange={(e) => setLoginPassword(e.target.value)}
            className="w-full p-2 border rounded mb-6"
            required
          />

          <button
            type="submit"
            className="w-full bg-blue-600 text-white p-2 rounded hover:bg-blue-700"
          >
            Login
          </button>
        </form>
      </div>
    );
  }

  /** 6️⃣ Dashboard view */
  return (
    <AdminLayoutClient>
      <div className="p-6">
        <h1 className="text-3xl font-bold mb-6">Admin Dashboard</h1>
        {fetchError && <p className="text-red-500 mb-4">{fetchError}</p>}
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
          <div className="bg-white shadow rounded-lg p-6 flex flex-col items-start">
            <h2 className="text-lg font-semibold text-gray-600 mb-2">Total Jobs</h2>
            <span className="text-3xl font-bold text-blue-600">{dashboardData?.totalJobs}</span>
          </div>
          <div className="bg-white shadow rounded-lg p-6 flex flex-col items-start">
            <h2 className="text-lg font-semibold text-gray-600 mb-2">Total Applications</h2>
            <span className="text-3xl font-bold text-green-600">{dashboardData?.totalApplications}</span>
          </div>
          <div className="bg-white shadow rounded-lg p-6 flex flex-col items-start">
            <h2 className="text-lg font-semibold text-gray-600 mb-2">Quick Actions</h2>
            <ul className="text-gray-700 list-disc list-inside">
              <li>Create Job</li>
              <li>Manage Applications</li>
              <li>Edit Pages</li>
            </ul>
          </div>
        </div>
      </div>
    </AdminLayoutClient>
  );
}
